html {
  font-size: $fz;
  line-height: 1.3;
  @include sm {
    font-size: $fz-sm-px; } }

body {
  background-color: $white;
  min-width: 320px;
  @include reg;
  color: $c-text;
  @include no-scroll; }


.icon {
  display: inline-block;
  fill: currentColor;
  font-size: inherit; }

.lazy {
  $this: &;
  &--loaded:not(#{$this}--no-fade-in) {
    animation: fadeIn 1s; } }

.container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 44px;
  padding-right: 44px;
  max-width: 1126px;
  &--lg {
    max-width: 1531px; }
  &--xl {

    max-width: 1749px; } }


.out {
  padding-top: $header-height; }

.descr {
  p {
    @include notlast {
      margin-bottom: 30px; } } }

