.post {
  background-color: $white;
  padding: 30px 15px 15px;
  color: $gray;
  @include shadow;
  &__title {
    margin-bottom: 25px;
    color: $c-text; }
  &__meta {
    margin-bottom: 15px; }
  &__img {
    @include cover;
    padding-top: 50%;
    margin-bottom: 100px;
    margin-left: -15px; } }

.post-meta {
  @include flex;
  &__item {
    @include inline-flex(flex-start, center);
    @include notlast {
      margin-left: 15px; }
    .icon {
      margin-left: .5em;
      font-size: .8em; } } }

